import './cost.css';
import { motion } from 'framer-motion';

 


const textAnimarion = {
    hidden: {
        x: -100,
        opacity: 0,
    },
    visible: custom => ({
        x: 0,
        opacity: 1,
        transition: { delay: custom*0.3 },
    }),
  }
const Cost =() => {
    return (
        <motion.div 
        initial="hidden"
        whileInView="visible"
        viewport={{ amount: 0.3, once: true }}
        className="cost">

            <div id='cost' className="parallax__six">
                <div className="cost__main">
                        <div className="container">
                            <div id='cost' className="headline_seconds">
                                <motion.h1 custom={1} variants={textAnimarion}>   </motion.h1>
                            </div>
                        </div>
                </div>
            </div>

            <div className="container">
                <motion.h2 custom={2} variants={textAnimarion} className="cost__headline">    2023/2024</motion.h2>
               

                <table>
                    <thead>
                    <tr>
                        <th className='th-top'> : , , , ,  (   c 10:00  16:00)</th>
                        <th className='th-top-right'></th>
                    </tr>
                    </thead>
                    <tbody>
                    <tr>
                        <td>Ski-pass  ( )</td>
                        <td>1 100 </td>
                        
                    </tr>
                    <tr>
                        <td>Ski-pass  c 3  11  ( )</td>
                        <td>800 </td>
                    </tr>
                    <tr>
                        <td>Ski-pass  1 </td>
                        <td>400 </td>
                    </tr>
                    <tr>
                        <td>Ski-pass  2 </td>
                        <td>800 </td>
                    </tr>
                    <tr>
                        <td> <strong> </strong> </td>
                        <td>300 </td>
                    </tr>
                    </tbody>

                    <thead>
                        <tr>
                            <th> :, <span>    (c 10:00  16:00)</span></th>
                            <th></th>
                        </tr>
                    </thead>
                    <tbody>
                    <tr>
                        <td>Ski-pass  ( )</td>
                        <td>1 500 </td>
                    </tr>
                    <tr>
                        <td>Ski-pass  c 3  11  ( )</td>
                        <td>1 100 </td>
                    </tr>
                    <tr>
                        <td>Ski-pass     c 1000.  1300.  c 1300.  1600.</td>
                        <td>1 200 </td>
                    </tr>
                    <tr>
                        <td>Ski-pass     c 1000.  1300.  c 1300.  1600.</td>
                        <td>800 </td>
                    </tr>
                    <tr>
                        <td>Ski-pass  1 </td>
                        <td>400 </td>
                    </tr>
                    <tr>
                        <td>Ski-pass  2 </td>
                        <td>800 </td>
                    </tr>
                    <tr>
                        <td> <strong> </strong> </td>
                        <td>300 </td>
                    </tr>
                    </tbody>
                </table>




                <motion.h2 custom={4} variants={textAnimarion} className="cost__headline">   ( 1000.  1600.) <br />
                (   ,  1    1 ) 
                </motion.h2>


                <table className='table__two'>
                    
                        <thead>
                        <tr>
                            <th className='th-top'> </th>
                            <th className='th-top-right'>  </th>
                        </tr>
                        </thead>
                        <tr>
                            <td>  (  2012 )</td>
                            <td>600 </td>
                            
                        </tr>
                        <tr>
                            <td>  (   2013-14 )</td>
                            <td>1000 </td>
                        </tr>
                        <tr>
                            <td>  (   2015-2016 )</td>
                            <td>1 200 </td>
                        </tr>
                  
                </table>

                <h3 className='bottom_h3'>               .</h3>
                
            </div>
        </motion.div>
    )
}

export default Cost